import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import Spotify from "../utils/Spotify";

const initialState = {
  term: "",
  songsFetchStatus: "not_ready",
  playlistStatus: "not_ready",
  results: {},
  addedToPlaylist: [],
  trackUris: [],
};

export const asyncSearchResults = createAsyncThunk(
  "songs/searchTerm",
  async (info) => {
    const response = await Spotify.search(info.query, info.token);
    const payload = {
      term: info.query,
      results: response,
    };
    return payload;
  }
);

export const songsSlice = createSlice({
  name: "songs",
  initialState,
  reducers: {
    setTerm: (state, action) => {
      state.term = action.payload;
    },
    addToCurrentPlaylist: (state, action) => {
      if (
        state.addedToPlaylist.find((track) => track.id === action.payload.id)
      ) {
        return;
      }
      console.log(action.payload);
      state.addedToPlaylist.push(action.payload);
      state.trackUris.push(action.payload.uri);
      state.playlistStatus = "ready";
    },
    removeFromCurrentPlaylist: (state, action) => {
      state.addedToPlaylist = state.addedToPlaylist.filter(
        (track) => track.id !== action.payload.id
      );
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(asyncSearchResults.pending, (state) => {
        state.songsFetchStatus = "pending";
      })
      .addCase(asyncSearchResults.fulfilled, (state, action) => {
        state.songsFetchStatus = "fetched";
        state.results[action.payload.term] = action.payload;
      });
  },
});

export const selectTerm = (state) => state.songs.term;
export const selectResults = (state) => state.songs.results;
export const selectStatus = (state) => state.songs.songsFetchStatus;
export const selectAddedToPlaylist = (state) => state.songs.addedToPlaylist;
export const selectPlaylistStatus = (state) => state.songs.playlistStatus;
export const selectTrackUris = (state) => state.songs.trackUris;

export const { setTerm, addToCurrentPlaylist, removeFromCurrentPlaylist } =
  songsSlice.actions;

export default songsSlice.reducer;
