//Basic Imports
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import "./App.css";
import "../main.css";
import { useCookies } from "react-cookie";
import { Buffer } from "buffer";
import { v4 as uuidv4 } from "uuid";
import Spotify from "../../../app/utils/Spotify";
import {
  selectToken,
  selectTokenStatus,
  setToken,
} from "../../../app/store/userSlice";

//Component Imports
import SearchBar from "../SearchBar/SearchBar";
import SearchResults from "../SearchResults/SearchResults";
import Playlist from "../Playlist/Playlist";

function App() {
  const current = new Date();
  const nextYear = new Date();
  const tomorrow = new Date();
  const id = uuidv4();
  var accessToken = "";

  nextYear.setFullYear(current.getFullYear() + 1);
  const [cookies, setCookie, removeCookies] = useCookies();
  const encodedToken = Buffer.from(``).toString("base64");

  //--- REDUX ---//
  const dispatch = useDispatch();
  const token = useSelector(selectToken);
  const tokenStatus = useSelector(selectTokenStatus);

  //--- Fetch access token to perform requests ---/

  const clientID = "c98ea7ef0ab94c0d9401589ed10ad372";
  const redirectURI = "http://localhost:3000/";

  const fetchAccessToken = () => {
    if (accessToken) {
      return accessToken;
    }

    //check for access token match
    var accessTokenMatch = window.location.href.match(/access_token=([^&]*)/);
    var expiresInMatch = window.location.href.match(/expires_in=([^&]*)/);

    if (accessTokenMatch && expiresInMatch) {
      accessToken = accessTokenMatch[1];
      let expiresIn = Number(expiresInMatch[1]);
      window.setTimeout(() => (accessToken = ""), expiresIn * 1000);
      window.history.pushState("Access Token", null, "/");
      return accessToken;
    } else {
      const accessURL = `https://accounts.spotify.com/authorize?client_id=${clientID}&response_type=token&scope=playlist-modify-private&redirect_uri=${redirectURI}`;
      window.location = accessURL;
    }
  };

  useEffect(() => {
    if (accessToken === "") {
      accessToken = fetchAccessToken();
      dispatch(setToken(accessToken));
    }
  }, []);

  //--- Look for cookie spoti_DB, if not found, create one ---/
  useEffect(() => {
    if (cookies.spoti_DB) {
    } else {
      console.log(tomorrow.getDate());
      tomorrow.setDate(current.getDate() + 1);
      console.log(tomorrow.getDate());
      setCookie("spoti_DB", {}, { expires: tomorrow });
    }
  }, []);

  return (
    <div className="App">
      <div className="Header">
        <header>Header</header>
      </div>
      <div className="Main-Container">
        <div className="SearchContainer">
          {tokenStatus === "ready" ? (
            <SearchBar token={token} />
          ) : (
            <button>Access Spotify to start looking for songs</button>
          )}
        </div>
        <div className="ListsContainer">
          <div className="SearchResultsContainer">
            <SearchResults />{" "}
          </div>
          <div className="AddedToPlaylistContainer">
            <Playlist />{" "}
          </div>
        </div>
        <div className="PlaylistsContainer"></div>
      </div>
    </div>
  );
}

export default App;
