let accessToken;
const clientID = "c98ea7ef0ab94c0d9401589ed10ad372";
const redirectURI = "http://localhost:3000/";

const Spotify = {
  getAccessToken() {
    if (accessToken) {
      return accessToken;
    }

    //check for access token match
    var accessTokenMatch = window.location.href.match(/access_token=([^&]*)/);
    var expiresInMatch = window.location.href.match(/expires_in=([^&]*)/);

    if (accessTokenMatch && expiresInMatch) {
      accessToken = accessTokenMatch[1];
      let expiresIn = Number(expiresInMatch[1]);
      window.setTimeout(() => (accessToken = ""), expiresIn * 1000);
      window.history.pushState("Access Token", null, "/");
      return accessToken;
    } else {
      const accessURL = `https://accounts.spotify.com/authorize?client_id=${clientID}&response_type=token&scope=playlist-modify-private&redirect_uri=${redirectURI}`;
      window.location = accessURL;
    }
  },

  search(term, token) {
    const accessToken = token;
    return fetch(`https://api.spotify.com/v1/search?type=track&q=${term}`, {
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
    })
      .then((response) => response.json())
      .then((jsonResponse) => {
        if (!jsonResponse.tracks === 0) {
          return [];
        }
        return jsonResponse.tracks.items.map((track) => ({
          name: track.name,
          id: track.id,
          artist: track.artists[0].name,
          album: track.album.name,
          uri: track.uri,
          image: {
            height: track.album.images[2].height,
            width: track.album.images[2].width,
            url: track.album.images[2].url,
          },
        }));
      });
  },

  savePlaylist(playlistName, tracksURIs, token) {
    if (!playlistName && !tracksURIs.length) {
      return;
    }
    var accessToken = token;
    var headers = {
      Authorization: `Bearer ${token}`,
    };

    var userID;
    return fetch("https://api.spotify.com/v1/me", {
      headers: headers,
    })
      .then((response) => response.json())
      .then((jsonResponse) => {
        userID = jsonResponse.id;
        return fetch(`https://api.spotify.com/v1/users/${userID}/playlists`, {
          headers: headers,
          method: "POST",
          body: JSON.stringify({
            name: `${playlistName}`,
            description: "This playlist was created with Jammming!",
            public: false,
          }),
        })
          .then((response) => response.json())
          .then((jsonResponse) => {
            var playlistID = jsonResponse.id;
            return fetch(
              `https://api.spotify.com/v1/playlists/${playlistID}/tracks`,
              {
                headers: headers,
                method: "POST",
                body: JSON.stringify({ uris: tracksURIs }),
              }
            )
              .then((response) => response.json())
              .then((jsonResponse) => {
                console.log(jsonResponse.snapshot_id);
                alert("Playlist Saved");
              });
          });
      });
  },
};

export default Spotify;
