//BAsic IMports
import React, { useState, useEffect } from "react";
import TrackList from "../TrackList/TrackList";
import "./Playlist.css";
import { useCookies } from "react-cookie";

import { useSelector, useDispatch } from "react-redux";
import {
  selectAddedToPlaylist,
  selectPlaylistStatus,
  selectTrackUris,
  addToCurrentPlaylist,
  removeFromCurrentPlaylist,
} from "../../../app/store/songsSlice";
import { selectToken } from "../../../app/store/userSlice";

import Spotify from "../../../app/utils/Spotify";

function Playlist() {
  const dispatch = useDispatch();

  const [name, setName] = useState("");
  const [cookies, setCookie, removeCookie] = useCookies();
  const [counter, setCounter] = useState(0);

  const addedToPlaylist = useSelector(selectAddedToPlaylist);
  const playlistStatus = useSelector(selectPlaylistStatus);
  const trackUris = useSelector(selectTrackUris);
  const token = useSelector(selectToken);

  const clearData = () => {
    addedToPlaylist.forEach((song) => {
      dispatch(removeFromCurrentPlaylist(song));
      removeCookie("spoti_DB");
      setName("");
    });
  };

  const onSave = () => {
    Spotify.savePlaylist(name, trackUris, token);
  };

  useEffect(() => {
    if (cookies.spoti_DB) {
      if (cookies.spoti_DB.playlist) {
        cookies.spoti_DB.playlist.forEach((song) => {
          return dispatch(addToCurrentPlaylist(song));
        });
      }
    }
  }, []);

  useEffect(() => {
    if (addedToPlaylist.length !== 0) {
      setCookie("spoti_DB", {
        ...cookies.spoti_DB,
        playlist: addedToPlaylist,
      });
    }
  }, [addedToPlaylist]);

  return (
    <div className="Playlist">
      <div className="Playlist-input">
        <input
          id="playlist-name"
          type="text"
          placeholder="Playlist Name"
          onChange={(e) => {
            setName(e.target.value);
          }}
        ></input>
        <button onClick={onSave}> Save Playlist </button>
      </div>
      <div>
        {playlistStatus === "ready" ? (
          <TrackList tracklist={addedToPlaylist} listType="playlist" />
        ) : (
          <div>
            <p>No Songs added to current Playlist</p>
          </div>
        )}
      </div>
      <div className="Playlist-Clear">
        <button
          onClick={() => {
            clearData();
          }}
        >
          {" "}
          Clear Playlist{" "}
        </button>
      </div>
    </div>
  );
}

export default Playlist;
