import React from "react";
import "./Track.css";

import { useDispatch } from "react-redux";

import {
  addToCurrentPlaylist,
  removeFromCurrentPlaylist,
} from "../../../app/store/songsSlice";

function Track(props) {
  const dispatch = useDispatch();

  const handleClickAdd = () => {
    dispatch(addToCurrentPlaylist(props));
  };

  const handleClickRemove = () => {
    dispatch(removeFromCurrentPlaylist(props));
  };

  return (
    <div className="Track">
      <div className="Track-image">
        <img src={props.image} alt="album cover" />
      </div>
      <div className="Track-info">
        <a id="title">Title: {props.name} </a>
        <br></br>
        <a id="artist">Artist: {props.artist}</a>
      </div>

      <div className="Track-add">
        {props.listType === "results" ? (
          <button onClick={handleClickAdd}> + </button>
        ) : (
          <button onClick={handleClickRemove}> - </button>
        )}
      </div>
    </div>
  );
}

export default Track;
